.drop-zone {
	width: 100%;
	max-width: 400px;
	height: 150px;
	border: 2px dashed #cccccc;
	border-radius: 10px;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	text-align: center;
	font-size: 1rem;
	color: #666666;
	background-color: #f9f9f9;
	transition: background-color 0.3s ease, border-color 0.3s ease;
	margin: 20px auto;
}

.drop-zone.dragging {
	background-color: #e6f7ff;
	border-color: #1890ff;
	color: #1890ff;
}

.upload-btn {
	margin-top: 10px;
	background-color: #007bff;
	color: white;
	padding: 10px 20px;
	border: none;
	border-radius: 5px;
	cursor: pointer;
	text-align: center;
	text-decoration: none;
	transition: background-color 0.3s ease;
}

.upload-btn:hover {
	background-color: #0056b3;
}

.upload-btn:active {
	background-color: #004085;
}

.load-data-h1 {
	padding-top: 1rem;
	text-align: center;
}

.csv-type-selector {
	text-align: center;
}

.csv-type-selector input[type="radio"] {
	margin: 0 5px;
}

.upload-status {
	text-align: center;
}